                h4 Desparasitacin interna y externa 
                p.mb-3 El control de parsitos internos y externos es esencial para la salud general y la eficiencia productiva del hato. Se recomienda: 
                <ul class="fa-ul estilo___i_1">
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span><strong>Desparasitacin interna:</strong> cada 3 - 6 meses, utilizando rotacin de principios activos para evitar resistencia.</li>
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span><strong>Desparasitacin externa:</strong> control de garrapatas y moscas mediante baos de aspersin o pour-on, mnimo cada 45 das.</li>
