                <ul class="fa-ul estilo___i_1 mb-0">
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Diagnstico y aislamiento de animales positivos a brucelosis y tuberculosis.</li>
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Registro y disposicin sanitaria de fetos abortados y fluidos contaminados.</li>
                  <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Prohibicin del consumo o comercializacin de leche de animales positivos sin tratamiento trmico.</li>
