        h3 Programa de control y prevencin de mastitis   

    p.mb-5 La mastitis es una enfermedad que compromete la salud de la ubre, afecta la productividad y puede poner en riesgo la inocuidad de la leche. Este programa se aplica a todas las vacas en produccin, al equipo de ordeo y al personal responsable del proceso de extraccin.  

    h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Conceptos fundamentales 

    p.mb-5 Antes de abordar el procedimiento, es necesario comprender los trminos clave: 

    .row.justify-content-center.mb-5
      .col-xl-10
        .row
          .col-xl-4(data-aos="zoom-in")
            .style___box_12.h-100.ajuste___color_1
              h4 Mastitis
              p.mb-0 Inflamacin de la glndula mamaria, causada generalmente por bacterias. Puede ser clnica (visible) o subclnica (sin signos externos).
          .col-xl-4(data-aos="zoom-in").espacio___movil_3
            .style___box_12.h-100.ajuste___color_2
              h4 CMT
              p.mb-0 <em>California Mastitis Test</em>. Prueba rpida para detectar mastitis subclnica en campo.
