    p.mb-5 Este protocolo est diseado para garantizar que la <strong>leche extrada de vacas bajo tratamiento veterinario</strong> no se mezcle con leche destinada al consumo humano, evitando as riesgos sanitarios. Su correcta implementacin contribuye al cumplimiento de la normativa y a la proteccin de la salud pblica. 

