                h4 Identificacin del animal tratado 
                p.mb-3 Para evitar errores en el manejo de leche, se deben seguir estas acciones: 
                <ul class="fa-ul estilo___i_1 mb-0">
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Identificar visiblemente al animal tratado (ejemplo: collar rojo, marca en la pierna, brida de color).</li>
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Registrar el tratamiento en el formato de control sanitario (FR-TRA-001), indicando:</li>
                  <ul class="fa-ul estilo___ul">
                    <li class="mb-0"><span class="fa-li"><i class="fab fa-redhat"></i></span>Nombre del medicamento.</li>
                    <li class="mb-0"><span class="fa-li"><i class="fab fa-redhat"></i></span>Fecha y hora de aplicacin.</li>
                    <li class="mb-0"><span class="fa-li"><i class="fab fa-redhat"></i></span>Dosis.</li>
                    <li class="mb-0"><span class="fa-li"><i class="fab fa-redhat"></i></span>Va de administracin.</li>
                    <li class="mb-0"><span class="fa-li"><i class="fab fa-redhat"></i></span>Tiempo de retiro establecido.</li>
                  </ul>
                </ul>
