                p.mb-3 Es necesario evitar el contacto entre la leche de retiro y el resto de la produccin: 
                <ul class="fa-ul estilo___i_1 mb-0">
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Ordear a las vacas tratadas al final de la jornada o en un sistema separado.</li>
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Usar baldes o lneas de ordeo exclusivos para esta leche.</li>
                  <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Desinfectar de inmediato el equipo utilizado: pezoneras, baldes, guantes, etc.</li>
                </ul>
