                p.mb-3 La leche obtenida durante el perodo de retiro no puede mezclarse con leche destinada al consumo o comercializacin. Las opciones de disposicin incluyen: 
                <ul class="fa-ul estilo___i_1 mb-0">
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Eliminacin controlada en pozos spticos o sistemas de compostaje.</li>
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Uso en la alimentacin de terneros, solo si el medicamento lo permite y bajo supervisin veterinaria.</li>
                  <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Prohibido el uso en animales de levante destinados a consumo humano si el medicamento representa un riesgo zoontico.</li>
