        h3 Procedimientos de limpieza y desinfeccin del equipo de ordeo y tanque  

    p.mb-5 Una correcta limpieza y desinfeccin es fundamental para prevenir la contaminacin microbiana en la leche y garantizar la calidad del producto final. 
    
    h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Materiales y productos necesarios 

    p.mb-5 Antes de iniciar el procedimiento, se debe disponer de los siguientes insumos: 

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.m-0.style___box_1_1.ajuste___color(data-aos="zoom-in")
          .col-xl
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Detergente <strong>alcalino o enzimtico</strong> para eliminar residuos orgnicos (grasas y protenas).</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>cido (ntrico o fosfrico) para remover <strong>depsitos minerales</strong>.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span><strong>Desinfectante aprobado</strong>: hipoclorito de sodio, yodo o perxido de hidrgeno.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Agua potable (fra y caliente).</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Cepillos y mangueras especficas.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Guantes, gafas y otros elementos de equipo de proteccin personal (EPP).</li>
            </ul>
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img76.jpg', alt='' style="max-width: 500px").m-auto

    h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Procedimiento de limpieza del equipo de ordeo 

    p.mb-5 El procedimiento aplica tanto para sistemas en lnea como para partes desmontables: 

    .row.mb-5
      .col-xl-auto
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img77.jpg', alt='' style="max-width: 500px").m-auto
      .col-xl
        ol.lista-ol--cuadro
          li(data-aos="fade-left")
            .lista-ol--cuadro__vineta
              span A
            | Desconectar el equipo y retirar completamente la leche residual. 
          li(data-aos="fade-left")
            .lista-ol--cuadro__vineta
              span B
            | Enjuagar con agua potable tibia (30 - 35 C). 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span C
            | Aplicar detergente alcalino o enzimtico circulando por 510 minutos. 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span D
            | Enjuagar con agua potable caliente (> 40 C). 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span E
            | Circular solucin cida durante 5 minutos para eliminar sarro o residuos minerales. 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span F
            | Enjuagar con abundante agua potable. 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span G
            | Lavar manualmente piezas desmontables con cepillo y detergente. 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span H
            | Sumergir las piezas en desinfectante (respetar concentracin y tiempo de contacto). 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span I
            | Enjuagar si es necesario (segn el tipo de desinfectante). 
          li(data-aos="fade-left") 
            .lista-ol--cuadro__vineta
              span J
            | Dejar secar en rea limpia antes del siguiente ordeo. 

    h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Limpieza y desinfeccin del tanque de enfriamiento  

    p.mb-5 Este proceso debe realizarse despus de cada jornada y con mantenimiento mensual adicional: 

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.m-0.style___box_1_1(data-aos="zoom-in")
          .col-xl
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Vaciar totalmente el tanque y retirar los residuos.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar con agua potable tibia.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Circular detergente alcalino por 10 - 15 minutos.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar con agua potable caliente.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Circular cida durante 5 - 10 minutos. </li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar completamente con agua potable.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Aplicar desinfectante y dejar actuar segn el tiempo indicado.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar si es necesario y dejar el tanque secar con la tapa abierta.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Realizar mantenimiento mensual de termostatos, sensores y sistema de enfriamiento.</li>
            </ul>
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img78.jpg', alt='' style="max-width: 500px").m-auto

    h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Medidas de seguridad  

