        h3 Procedimiento para la compra de animales

    p.mb-5 Este procedimiento establece los pasos que deben seguirse para asegurar la adquisicin de animales sanos, trazables y con valor productivo y gentico, cumpliendo con la normatividad vigente del ICA y las buenas prcticas ganaderas. 

    .fondo___slider.mb-5
      .px-5
        .ajuste-slider(data-aos="zoom-in")
          SlyderA(tipo="b")
            .row.h-100.justify-content-center.align-items-center
              .col-xl-6.mb-4
                h4 Identificacin de la necesidad 
                p.mb-3 Antes de iniciar el proceso de compra, se realiza una evaluacin tcnica del hato para determinar:
                <ul class="fa-ul estilo___i_1 mb-0">
                  <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Nmero de animales a adquirir.</li>
                  <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Sexo, raza y caractersticas productivas y genticas requeridas.</li>
                </ul>
              .col-xl-6
                figure.d-none.d-xl-block
                  img(src='@/assets/curso/temas/img83.png', alt='' style="max-width: 500px").m-auto
            
            .row.h-100.justify-content-center.align-items-center
              .col-xl-6.mb-4
                h4 Seleccin del proveedor 
                p.mb-3 Se deben elegir proveedores confiables. Los criterios son:
                <ul class="fa-ul estilo___i_1 mb-0">
