        h3 Procedimiento de limpieza del cuarto del tanque y sitio de ordeo 

    p.mb-5 Este procedimiento garantiza condiciones higinicas adecuadas en las reas donde se almacena y manipula la leche, contribuyendo directamente a la inocuidad del producto y al cumplimiento de las buenas prcticas ganaderas. 

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.m-0.style___box_14(data-aos="zoom-in")
