            h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Alcance 
            p.mb-0 Aplica a <strong>todas las actividades de limpieza y desinfeccin</strong> del cuarto de almacenamiento del tanque de leche y del sitio de ordeo, incluyendo pisos, paredes, equipos fijos y mobiliario. 
