              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Desinfectante aprobado (hipoclorito, yodo, amonios cuaternarios, etc.).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Cepillos y mopas de uso exclusivo para el rea de ordeo.</li>
