    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Procedimiento paso a paso 

    p.mb-5 A continuacin, se describe detalladamente cada una de las etapas que deben cumplirse para una limpieza y desinfeccin efectiva: 

    TabsA.color-acento-contenido.mb-5(data-aos="zoom-in")
