              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Retirar residuos visibles de leche, polvo, estircol y suciedad.</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Sacar los utensilios y equipos porttiles del rea a limpiar.</li>
