              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Humedecer las superficies con agua potable. </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Aplicar detergente en pisos, paredes, canaletas, mesas, mobiliario. </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Frotar con cepillos o mopas, enfocndose en rincones y esquinas.</li>
