            figure.d-none.d-xl-block
              img(src="@/assets/curso/temas/img92.png", alt="" style="max-width: 400px").m-auto
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Enjuague")
        .row.align-items-center
          .col-lg
            h4 Enjuague
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar con abundante agua potable para eliminar el detergente y los residuos orgnicos.</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Verificar que no queden restos visibles ni olores.</li>
