              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Dejar actuar durante el tiempo recomendado por el fabricante (10 - 30 minutos). </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar si es requerido por el producto utilizado.</li>
