              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Dejar secar al aire o con trapos limpios. </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Asegurar buena ventilacin para evitar acumulacin de humedad.</li>
