    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Frecuencia  

    p.mb-5 La limpieza debe realizarse con una frecuencia mnima definida segn el uso de las instalaciones: 

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.m-0.style___box_1_1(data-aos="zoom-in")
