              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span><strong>Al finalizar cada jornada de ordeo</strong>: se debe hacer limpieza y desinfeccin completa de todas las superficies y equipos.</li>
              <li class="mb-2"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span><strong>Una vez por semana</strong>: realizar una <strong>limpieza profunda</strong> de paredes, techos, reas de difcil acceso o segn indicaciones de control sanitario.</li>
            </ul>
