        h3 Limpieza y desinfeccin de utensilios y elementos usados en el ordeo 

    p.mb-5 Esta actividad es clave para garantizar la inocuidad de la leche. Abarca todos los elementos manuales utilizados durante el proceso de ordeo, los cuales deben mantenerse en condiciones higinicas ptimas para evitar contaminacin cruzada. 

    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Materiales y equipos necesarios 

    p.mb-5 Antes de iniciar la limpieza, es indispensable contar con los siguientes insumos: 

    .row.justify-content-center.mb-5.m-0
      .col-xl-10
        .row.align-items-center.style___box_13(data-aos="zoom-in")
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img99.jpg', alt='' style="max-width: 500px").m-auto
          .col-xl
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Agua potable limpia y caliente (idealmente a 70 C). </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Detergente alcalino o neutro aprobado para uso agroalimentario. </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Desinfectante autorizado (hipoclorito, yodo, amonios cuaternarios, etc.). </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Cepillos y esponjas de uso exclusivo para utensilios de ordeo. </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Toallas limpias o trapos de algodn. </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Estantes o armarios limpios, elevados y ventilados. </li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Guantes y equipo de proteccin personal (EPP). </li>
            </ul>

    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Procedimiento paso a paso

    p.mb-5 A continuacin, se detallan las etapas recomendadas para una limpieza y desinfeccin eficaz: 

    .row.justify-content-center
      .col-xl-10
        .bg--img_03
          .row.h-100.align-items-center
            .col-xl-auto
              figure(data-aos="fade-right").d-none.d-xl-block
                img(src="@/assets/curso/temas/img100.png", alt="" style="width: 400px").m-auto
            .col-xl
              AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-left")
                .row(titulo="Preenjuague").ajuste-cajaAcordion
                  p.mb-0 Enjuagar los utensilios inmediatamente despus del ordeo con agua potable para eliminar residuos de leche. 
                .row(titulo="Lavado").ajuste-cajaAcordion
                  p.mb-0 Lavar con detergente alcalino o neutro utilizando cepillos o esponjas, asegurndose de limpiar zonas de difcil acceso y juntas. 
                .row(titulo="Enjuague").ajuste-cajaAcordion
                  p.mb-0 Enjuagar con agua potable caliente para eliminar completamente los restos del detergente.
                .row(titulo="Desinfeccin").ajuste-cajaAcordion
                  p.mb-0 Sumergir o rociar los utensilios con desinfectante en la concentracin adecuada. Dejar actuar segn las recomendaciones del fabricante. 
        
        .bg--img_03.espacio___movil_3
          .row.h-100.align-items-center
            .col-xl
              AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-left")
                .row(titulo="Enjuague final (si aplica)").ajuste-cajaAcordion
                  p.mb-0 Enjuagar con agua potable si el desinfectante utilizado lo requiere. 
                .row(titulo="Secado").ajuste-cajaAcordion
                  p.mb-2 Una vez limpios, los utensilios deben: 
                  <ul class="fa-ul estilo___li mb-0">
                    <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Secarse con <strong>toallas limpias</strong> o al aire, en un lugar <strong>ventilado y libre de polvo</strong>. </li>
                    <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Nunca estar en contacto con el suelo ni con superficies contaminadas. </li>
                  </ul>
                .row(titulo="Almacenamiento").ajuste-cajaAcordion
                  p.mb-2 Despus del secado, los utensilios deben guardarse: 
                  <ul class="fa-ul estilo___li mb-0">
                    <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>En <strong>estantes elevados o armarios limpios</strong>, protegidos del polvo, insectos y animales. </li>
                    <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>En un rea ventilada, seca y ordenada. </li>
                  </ul>
                .row(titulo="Control y supervisin").ajuste-cajaAcordion
                  p.mb-2 Para asegurar el cumplimiento del procedimiento:  
                  <ul class="fa-ul estilo___li mb-0">
                    <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Realizar inspecciones visuales diarias. </li>
                    <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span><strong>Reparar o reemplazar</strong> utensilios deteriorados, sucios o con grietas que puedan acumular suciedad o bacterias.</li>
                  </ul>
