        h3 Manejo de enfermedades endmicas y de control oficial 

    p.mb-5 Las enfermedades endmicas y de control oficial representan un riesgo sanitario, productivo y comercial para los sistemas bovinos. Su vigilancia, prevencin y control deben realizarse bajo criterios tcnicos, normativos y con trazabilidad documentada. 

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.m-0.style___box_14(data-aos="zoom-in")
          .col-xl
            h4.mb-2.caja-titulos-cuarto-nivel(data-aos="fade-right") Alcance 
            p.mb-0 Este protocolo aplica al manejo sanitario de enfermedades como <strong>brucelosis, tuberculosis, fiebre aftosa, leucosis bovina, paratuberculosis</strong>, y otras clasificadas por el ICA como de control oficial o endmicas en bovinos. 
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img90.jpg', alt='' style="max-width: 500px").m-auto

    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Procedimiento 

    p.mb-5 A continuacin se detallan las acciones esenciales para un manejo efectivo de estas enfermedades:

    .bg--img_03.mb-5
      .row.h-100.align-items-center
        .col-xl-auto
          figure(data-aos="fade-right").d-none.d-xl-block
            img(src="@/assets/curso/temas/img103.png", alt="" style="width: 400px").m-auto
        .col-xl
          AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-left")
            .row(titulo="Vigilancia sanitaria").ajuste-cajaAcordion
              p.mb-0 Realizar monitoreo clnico diario para la deteccin temprana de signos compatibles. Reportar de inmediato al mdico veterinario y al ICA. 
            .row(titulo="Diagnstico").ajuste-cajaAcordion
              p.mb-0 Tomar muestras para pruebas autorizadas (serolgicas, intradrmicas, PCR, etc.). Enviar las muestras a laboratorios aprobados por el ICA. 
            .row(titulo="Prevencin y control").ajuste-cajaAcordion
              p.mb-0 Implementar programas de vacunacin obligatoria segn el calendario oficial (ej. brucelosis, aftosa). Establecer medidas de bioseguridad y control de ingreso. 
            .row(titulo="Manejo de casos positivos").ajuste-cajaAcordion
              p.mb-0 Aislar animales sospechosos o confirmados. Notificar a la autoridad. Aplicar cuarentena o sacrificio, segn normativa. Llevar registro clnico y teraputico. 
            .row(titulo="Eliminacin y disposicin final").ajuste-cajaAcordion
              p.mb-0 Cumplir con las disposiciones legales para sacrificio sanitario y eliminacin de cadveres. Desinfectar profundamente las reas afectadas.
            .row(titulo="Capacitacin y concienciacin").ajuste-cajaAcordion
              p.mb-0 Formar al personal en identificacin y control de enfermedades. Promover la cultura de denuncia y el cumplimiento normativo. 
            .row(titulo="Registro y trazabilidad").ajuste-cajaAcordion
              p.mb-0 Mantener registros actualizados de vacunaciones, diagnsticos, tratamientos, cuarentenas y movimientos. Usar formatos oficiales y sistemas del ICA. 
    
