          img(src="@/assets/curso/temas/img142.svg" style="max-width: 60px").m-auto
      .col
        h3 Procedimientos de limpieza y desinfeccin del tanque y equipo de ordeo

    p.mb-5 Para garantizar la inocuidad de la leche y la durabilidad del equipo, es indispensable realizar una limpieza y desinfeccin sistemtica del sistema de ordeo. 

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.m-0.style___box_14(data-aos="zoom-in")
          .col-xl
            h4.mb-2.caja-titulos-cuarto-nivel(data-aos="fade-right") Alcance 
            p.mb-0 Este procedimiento aplica a todos los elementos del sistema: unidad de ordeo (pezoneras, colectores, mangueras), tuberas, equipo de vaco, bomba de leche, tarros, baldes y el <strong>tanque de enfriamiento</strong>.
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img104.jpg', alt='' style="max-width: 500px").m-auto

    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Materiales requeridos

    p.mb-5 Se deben disponer los siguientes insumos y herramientas: 

    .row.justify-content-center.mb-5.m-0
      .col-xl-10
        .row.align-items-center.style___box_13(data-aos="zoom-in")
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img105.jpg', alt='' style="max-width: 500px").m-auto
          .col-xl
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Agua potable caliente (60 - 70 C).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Detergente alcalino (uso diario).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Detergente cido (uso semanal).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Desinfectante aprobado (hipoclorito de sodio, cido peractico o yodado).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Cepillos de cerdas plsticas y esponjas.</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Guantes y equipo de proteccin personal (EPP).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Cronmetro o temporizador.</li>
            </ul>

    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Limpieza y desinfeccin del sistema de ordeo 

    p.mb-5 Este procedimiento debe realizarse <strong>despus de cada jornada de ordeo</strong>, siguiendo los pasos detallados a continuacin: 

    .fondo___img
      .px-5
        SlyderF(columnas="col-lg-6 col-xl-4")(data-aos="zoom-in").mb-5
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/img148.svg' style="max-width: 90px").m-auto
            h4.mb-3.estilo-text Preenjuague
            p.mb-0 Enjuagar todo el sistema con agua tibia (35 - 40 C) inmediatamente despus del ordeo para remover residuos de leche.
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/img149.svg' style="max-width: 90px").m-auto
            h4.mb-3.estilo-text Lavado con detergente
            p.mb-0 Recircular solucin de detergente alcalino en agua caliente(60 - 70 C) por 10 - 15 minutos. Si se realiza limpieza manual, cepillar cada componente.
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/img150.svg' style="max-width: 90px").m-auto
            h4.mb-3.estilo-text Enjuaue
            p.mb-0 Enjuagar con abundante agua potable para eliminar residuos del detergente.
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/img151.svg' style="max-width: 90px").m-auto
            h4.mb-3.estilo-text Desinfeccin 
            p.mb-0 Aplicar desinfectante aprobado mediante recirculacin o aspersin. Dejar actuar al menos 5 minutos.
          .tarjeta.color-acento-botones.p-4
            .row.justify-content-center.mb-3
              .col-8
                img(src='@/assets/curso/temas/img152.svg' style="max-width: 90px").m-auto
            h4.mb-3.estilo-text Secado
            p.mb-0 Dejar secar al aire. No utilizar trapos contaminados.
        
        h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Limpieza del tanque de enfriamiento  

        p.mb-5 Este procedimiento debe realizarse <strong>despus de cada recoleccin de leche</strong>, y de forma ms profunda semanalmente: 

        .row.align-items-center.m-0.style___box_1_1.ajuste___color.mb-5(data-aos="zoom-in")
          .col-xl
            h5.mb-2.mt-2 Despus de cada recoleccin: 
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Vaciar completamente el tanque.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar con agua potable tibia.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Lavar con detergente alcalino caliente utilizando cepillos no abrasivos.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuagar con agua potable.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Aplicar desinfectante aprobado.</li>
              <li class="mb-3"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Dejar secar al aire.</li>
            </ul>
            h5.mb-2 Limpieza semanal: 
            <ul class="fa-ul estilo___ul mb-2">
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Utilizar detergente cido para eliminar residuos minerales (piedra de leche).</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Limpiar a fondo el agitador, vlvulas y zonas internas ocultas del tanque.</li>
            </ul>
