        h3 Manejo de cuarentena 

    p.mb-5 La cuarentena es una herramienta fundamental en los programas de bioseguridad pecuaria. Permite contener enfermedades potenciales antes del ingreso de nuevos animales al hato o en situaciones de sospecha clnica. 

    .row.justify-content-center.mb-5.m-0
      .col-xl-10
        .row.align-items-center.style___box_13(data-aos="zoom-in")
          .col-xl-auto
