          .col-xl
            h4.mb-2.caja-titulos-cuarto-nivel(data-aos="fade-right") Alcance 
            p.mb-0 Aplica a todos los animales que ingresan al predio, as como a animales del hato que presenten sntomas sospechosos de enfermedades infectocontagiosas, zoonticas o de control oficial. 

    h4.mb-4.caja-titulos-cuarto-nivel(data-aos="fade-right") Requisitos mnimos de infraestructura 

    p.mb-5 Para garantizar un aislamiento efectivo, el rea de cuarentena debe cumplir con las siguientes condiciones: 

