              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Punto de lavado de botas, ropa y manos.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Sistema de manejo de excretas y residuos.</li>
