        h3 Lavado de recipientes de leche de retiro 

    p.mb-5 El correcto lavado y desinfeccin de los recipientes utilizados para leche de retiro es clave para prevenir riesgos sanitarios y evitar la contaminacin cruzada con leche destinada al consumo humano. 

