                <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Evitar mezclar con utensilios de leche comercial.</li>
                <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Almacenar en sitio separado y ventilado.</li>
              </ul>
            .row(titulo="Lavado y desinfeccin").ajuste-cajaAcordion
              <ul class="fa-ul estilo___li mb-0">
