                <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuague intermedio.</li>
                <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Aplicacin de desinfectante (mnimo 5 min).</li>
                <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Enjuague final si es necesario.</li>
                <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Secado boca abajo en estantera elevada.</li>
              </ul>
            .row(titulo="Almacenamiento final").ajuste-cajaAcordion
              <ul class="fa-ul estilo___li mb-0">
