        .style___box_12.h-100.ajuste___color_1
          h5 Rotacin de potreros
          p.mb-0 Dividir el rea en lotes y rotar el ganado para evitar sobrepastoreo y mantener la calidad del forraje.
      .col-xl-3(data-aos="zoom-in").espacio___movil_3
        .style___box_12.h-100.ajuste___color_2
          h5 Descanso del potrero
          p.mb-0 Dejar entre 20 y 40 das de descanso, segn el clima y tipo de pasto.
      .col-xl-3(data-aos="zoom-in").espacio___movil_3
        .style___box_12.h-100.ajuste___color_1
          h5 Evitar encharcamiento
          p.mb-0 Asegurar buen drenaje para prevenir enfermedades y deterioro del suelo.
      .col-xl-3(data-aos="zoom-in").espacio___movil_3
        .style___box_12.h-100.ajuste___color_2
          h5 Cercas en buen estado
          p.mb-0 Mantener las cercas reparadas para evitar el ingreso o salida no deseada de animales.

    .row.mb-5
      .col-xl-3(data-aos="zoom-in")
        .style___box_12.h-100.ajuste___color_2
          h5 Agua y sombra disponible
          p.mb-0 Garantizar acceso a agua limpia y sombra (natural o artificial) en cada potrero.
      .col-xl-3(data-aos="zoom-in").espacio___movil_3
        .style___box_12.h-100.ajuste___color_1
          h5 Control de malezas y plagas
