        h3 Registro de las pruebas de mastitis (California Mastitis Test, CMT)

    p.mb-5 El CMT permite detectar de forma rpida y econmica la mastitis subclnica en vacas en ordeo. El resultado se registra para tomar decisiones sanitarias oportunas. Los pasos para realizar y registrar la prueba son:

    .row.mb-3
      .col-xl-3(data-aos="zoom-in")
        .style___box_12.h-100.ajuste___color_10
          h5 Materiales
          p.mb-0 Paleta CMT (4 pocillos), reactivo CMT, guantes limpios, marcador, hoja de registro.
      .col-xl-3(data-aos="zoom-in").espacio___movil_3
        .style___box_12.h-100.ajuste___color_2
          h5 Preparacin de la muestra
          p.mb-0 Limpiar pezones, descartar primeros chorros, extraer leche de cada cuatro.
      .col-xl-3(data-aos="zoom-in").espacio___movil_3
        .style___box_12.h-100.ajuste___color_10
          h5 Aplicacin del reactivo
          p.mb-0 Aadir igual cantidad de reactivo que de leche en cada pocillo.
      .col-xl-3(data-aos="zoom-in").espacio___movil_3
        .style___box_12.h-100.ajuste___color_2
          h5 Mezcla
          p.mb-0 Mover en crculos suaves por 10 segundos.
    
    .row.justify-content-center.mb-5
      .col-xl-10
        .row
          .col-xl-4(data-aos="zoom-in")
            .style___box_12.h-100.ajuste___color_2
              h5 Observacin de resultados
              p.mb-0 Mezcla lquida: negativo; mezcla espesa o gelatinosa: positivo (nivel + a +++).
          .col-xl-4(data-aos="zoom-in").espacio___movil_3
            .style___box_12.h-100.ajuste___color_10
              h5 Registro
              p.mb-0 Anotar fecha, nmero del animal, cuatro(s) afectados y resultado.
          .col-xl-4(data-aos="zoom-in").espacio___movil_3
            .style___box_12.h-100.ajuste___color_2
              h5 Accin segn resultado
              p.mb-0 Si es positivo: tratamiento, aislamiento, descarte de leche, seguimiento.
    
    .row.m-0.mb-5.align-items-center.style___box_13.ajuste___color_9(data-aos="zoom-in")
      .col-xl-auto
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img126.jpg', alt='' style="max-width: 500px").m-auto
      .col-xl
        <ul class="fa-ul estilo___ul mb-0">
          <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Realizar la prueba una vez al mes o cuando se sospeche de mastitis.</li>
          <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Utilizar siempre material limpio y desinfectado.</li>
          <li class="mb-1"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>No mezclar la leche de vacas con resultado positivo.</li>
          <li class="mb-0"><span class="fa-li"><i class="fas fa-hat-cowboy"></i></span>Capacitar al personal en la aplicacin correcta del mtodo.</li>
        </ul>

